<TopAppBar variant="static">
	<Row>
		<Section>
			<IconButton on:click={() => (aberto = !aberto)} class="material-icons">menu</IconButton>
			<TABTitle>{titulo} - Tabela de  {tabelas[tabelaAtiva].nome}</TABTitle>
		</Section>
	</Row>
</TopAppBar>

<Drawer variant="modal" fixed={false} bind:open={aberto}>
	<Content>
		<List>
			{#each tabelas as tabela, iTab}
				<Item
					href="javascript:void(0)"
					on:click={() => ativarTabela(iTab)}
					activated={tabelaAtiva === iTab}
				>
					{#if tabela.mdi}	
						<Graphic class={'mdi mdi-' + tabela.icon} aria-hidden="true"></Graphic>
					{:else}
						<Graphic class="material-icons" aria-hidden="true">{tabela.icon}</Graphic>
					{/if}
					<Text>{tabela.nome}</Text>
				</Item>
			{/each}
		</List>
	</Content>
</Drawer>
<Scrim fixed={false} />
<AppContent class="app-content">
	<main class="main-content">
		<DataTable stickyHeader style="width: 100%;">
			<Head>
				<TRow>
					{#each tabelas[tabelaAtiva].cabecalho as campo}
						<Cell style="text-transform: capitalize;">{campo}</Cell>
					{/each}
				</TRow>
			</Head>
			<Body>
				{#each tabelas[tabelaAtiva].dados as item (item["id"])}
					<TRow>
						{#each tabelas[tabelaAtiva].cabecalho as campo}
							<Cell>{item[ campo ]}</Cell>
						{/each}
					</TRow>
				{/each}
			</Body>
		</DataTable>		
	</main>
</AppContent>
 
<script lang="ts">
 	import TopAppBar, { Row, Section, Title as TABTitle } from '@smui/top-app-bar';
  import IconButton from '@smui/icon-button';
	import Drawer, {
    AppContent,
    Content,
		Scrim
  } from '@smui/drawer';
  import List, { Item, Text, Graphic } from '@smui/list';
  import DataTable, { Head, Body, Row as TRow, Cell } from '@smui/data-table';
  
	let titulo = 'Cálculo de Custo';

  type Tabela = {
    mdi: boolean;
		icon: string;
    nome: string;
    cabecalho: string[];
    dados: object[];
  };
	let aberto = false;
 	let tabelas: Tabela[] = [
    {
      mdi: true,
      icon: 'food',
      nome: 'Produtos Finais',
			cabecalho: [ "id", "nome", "valor" ],
			dados: []
    },
    {
      mdi: false,
			icon: 'factory',
      nome: 'Produtos Intermediários',
			cabecalho: [ "id", "nome", "unidade", "valor" ],
			dados: []
    },
    {
      mdi: true,
      icon: 'puzzle',
      nome: 'Insumos',
			cabecalho: [ "id", "nome", "unidade", "valor" ],
			dados: []
    }
  ];
  let tabelaAtiva = 0;
  
	function ativarTabela(indice: number): void
	{
    tabelaAtiva = indice;
    aberto = false;
  }
</script>
 
<svelte:head>
	<!-- Fonts -->
  <link rel="stylesheet" href="https://fonts.googleapis.com/icon?family=Material+Icons">
	<link rel="stylesheet" href="https://cdn.jsdelivr.net/npm/@mdi/font@6.5.95/css/materialdesignicons.min.css">
  <link rel="stylesheet" href="https://fonts.googleapis.com/css?family=Roboto:300,400,500,600,700">
	
  <!-- Material Typography -->
	<link rel="stylesheet" href="https://unpkg.com/@material/typography@13.0.0/dist/mdc.typography.css" />
	
	<!-- SMUI -->
	<link rel="stylesheet" href="https://unpkg.com/svelte-material-ui/bare.css" />

	<title>{titulo}</title>
</svelte:head>